import React, { Component } from 'react'
import Login from "./Login"
import Register from "./Register"

export class Form extends Component {

    constructor(props){
        super(props);
        this.views = [<Login show={true} />, <Register show={false}/>]
        this.state = {
            index: 0,
            current: this.views[0]
        }
    }

    render() {
        if (this.state.current.show === false){
            this.setState({
                current: this.views[1 - this.index],
                index: 1 - this.index
            })
        }
        return (
            <div>
                {this.state.current}
            </div>
        )
    }
}

export default Form