import React, { Component } from 'react'
import Error from './Error';
import axios from 'axios';
import './form.css';
import {Link} from "react-router-dom";

export class Login extends Component {

    constructor(props){
        super(props);
        this.state = {
            show: this.props.show,
            error: false
        };
    }

    login(){
        axios.get('/ftd/api/user/')
        .then(
            (response) => {
                if(response.success === false){
                    //Now we wanna switch to the game ui.
                    this.setState({
                        show: false,
                        error: true
                    })
                }
            }
        )
    }

    render() {
        return (
            <div className='form'>
                <h1>Welcome to FTD</h1>
                <br></br>
                <div id="logindiv">
                LOGIN
                <br></br>
                <br></br>
                Username: <input type='text' id='loginUsername' />
                <br></br>
                <br></br>
                Password: <input type='password' id='loginPassword' />
                <br></br>
                <br></br>
                <button type='submit' id='loginSubmit' /*onClick={this.login()}*/>Login</button>
                <br></br>
                <br></br>
                <button type='submit' id='create'><Link to="/register">Don't have an account? Click here!</Link> </button>
                </div>
                <Error show={this.state.error} msg='Login Credentials are incorrect' />
                <br></br>
                <div id='highscores'></div>
            </div>
        )
    }
}

export default Login